@import '../../../../../styles/utils/variables', '../../../../../styles/utils/mixins';

.data_container {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: flex-end;
}

.data_table {
  width: 100%;
  font-size: 14px;

  thead {
    tr > td:last-child {
      text-align: right;
    }
  }

  tbody {
    tr {
      td {
        border-bottom: 1px solid $base-dark2-gray;
      }

      & > td:last-child {
        text-align: right;
      }
    }

    & > tr:last-child {
      td {
        border-bottom: none;
      }
    }
  }
}

.data_table_header {
  text-align: left;
  font-weight: normal;
  display: contents;
  color: $base-black;
  vertical-align: middle;

  @include darkTheme {
    color: $moderate-blue;
  }
}

.data_table_header td {
  height: 32px;
  border-bottom: 1px solid $base-dark2-gray;
  font-weight: bold;

  @include darkTheme {
    border-color: $grey;
  }
}

.data_table_tbody {
  color: $base-black;

  @include darkTheme {
    color: $white;
  }
}

.data_table_tbody td {
  width: 25%;
  height: 32px;
}

.data_action_button {
  margin-top: 50px;
  margin-bottom: 10px;
  width: 250px;
  height: 45px;
  line-height: 24px;

  @include respond-to('medium') {
    margin-top: 30px;
    width: 180px;
    height: 30px;
  }
}

.data_action_button span {
  font-size: 16px;
  line-height: 14px;

  @include respond-to('medium') {
    font-size: 11px;
  }
}

.labelContainer {
  justify-content: flex-start;
}

.totalCost {
  display: flex;
  justify-content: right;
  align-items: end;
  font-size: 14px;
  line-height: 24px;
}

.label {
  font-size: 16px;
  font-weight: 700;
  padding-right: 16px;
}
