@import 'utils/variables', 'utils/mixins';

.data_container {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: flex-end;
}

.data_input {
  display: flex;
  flex-direction: row;
  justify-content: space-between !important;
  align-items: flex-end;
  padding-top: 30px;

  @include respond-to('medium') {
    padding-top: 25px;
  }
}

.data_table {
  width: 100%;
  font-size: 18px;

  @include respond-to('medium') {
    font-size: 12px;
  }
}

.data_table_header {
  text-align: left;
  font-weight: normal;
  display: contents;
  color: $base-black;
  vertical-align: middle;
  font-size: 14px;

  @include darkTheme {
    color: $moderate-blue;
  }
}

.data_table_header td {
  height: 32px;
  border-bottom: 1px solid $base-dark2-gray;
  font-weight: bold;
}

.data_table_tbody {
  color: #313131;
  font-size: 14px;

  td {
    height: 32px;
    border-bottom: 1px solid $base-dark2-gray;
  }

  & > tr:last-child td {
    border-bottom: 0;
  }

  @include darkTheme {
    color: $white;
  }
}

.data_big {
  width: 100%;
  height: 67px;

  @include respond-to('medium') {
    height: 35px;
  }
}

.data_big span {
  font-size: 17px;

  @include respond-to('medium') {
    font-size: 12px;
  }
}

.data_label {
  font-size: 14px;
  line-height: 18px;
  color: #064fa3;

  @include respond-to('medium') {
    font-size: 11px;
    line-height: 13px;
  }
}

.labelContainer {
  justify-content: flex-start;
}

.icon_person {
  margin-right: 8px;
}

.user {
  display: flex;
}
