@import 'utils/variables', 'utils/mixins';

.wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding-bottom: 30px;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.dropdownContainerDiv {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.containerMinorDropdown {
  width: 400px;
  flex-direction: column;
  align-items: flex-start;

  @include respond-to('large') {
    width: 395px;
  }

  @include respond-to('medium') {
    width: 273px;
  }
}
.containerMinor {
  width: 400px;

  margin-top: 17px;

  flex-direction: column;
  align-items: flex-start;

  @include respond-to('large') {
    margin-top: 15px;

    width: 395px;
  }

  @include respond-to('medium') {
    margin-top: 10px;

    width: 273px;
  }
}

.dropdownContainer {
  margin-top: 8px;
  width: 100%;
  min-height: 45px;
  height: 45px;

  display: flex;
  align-items: center;

  padding: 8px;

  display: flex;
  justify-content: space-between;

  border: 1px solid $light-blue;
  box-sizing: border-box;
  border-radius: 8px;

  @include respond-to('large') {
    min-height: 40px;
    height: 40px;
    border-radius: 6px;
  }

  @include respond-to('medium') {
    min-height: 25px;
    height: 25px;
    border-radius: 4px;
  }
}

.dropdownLabel {
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: $dark-blue;

  @include respond-to('medium') {
    font-size: 10px;
    line-height: 12px;
  }

  @include darkTheme {
    color: $moderate-blue;
  }
}

.dropdownContainer {
  padding: 12px;

  @include darkTheme {
    background: $black-grey-light;
  }
}

.dropdownValue {
  font-size: 16px;
  line-height: 20px;
  font-weight: 300;
  color: $light-grey;

  @include respond-to('medium') {
    font-size: 10.5px;
    line-height: 12px;
  }

  @include darkTheme {
    color: $grey;
  }
}

.dataHeader {
  margin-top: 30px;

  font-weight: 700;
  font-size: 16px;
  line-height: 28px;
  color: $base-black;

  @include respond-to('large') {
    margin-top: 45px;
  }

  @include respond-to('medium') {
    margin-top: 20px;

    line-height: 15px;

    &:last-child {
      margin-top: 30px;
    }
  }

  @include darkTheme {
    color: $grey;
  }
}

.tablePeriod {
  width: 100%;
  font-size: 18px;
  padding-top: 20px;

  @include respond-to('medium') {
    font-size: 12px;
  }
}

.tablePeriodHeader {
  text-align: right;
  font-weight: normal;
  display: contents;
  color: #064fa3;
  vertical-align: middle;

  td {
    height: 75px;
    border-bottom: 1px solid #d2e8ff;
    font-weight: 600;
    padding-right: 10px;

    &:nth-child(1) {
      width: 15%;
    }

    &:nth-child(2) {
      width: 28%;
    }

    &:nth-child(3) {
      width: 28%;
    }

    &:nth-child(4) {
      width: 28%;
    }

    @include respond-to('medium') {
      height: 55px;
    }
  }

  @include darkTheme {
    color: $moderate-blue;
  }
}

.tablePeriodTbody {
  color: #2a2f42;
  font-size: 16px;
  text-align: right;

  @include respond-to('medium') {
    font-size: 10px;
  }

  tr {
    height: 60px;

    @include respond-to('medium') {
      height: 35px;
    }
  }

  tr td {
    border-bottom: 1px solid #d2e8ff;
    padding-right: 10px;
  }

  @include darkTheme {
    color: $white;
  }
}

.multiple_icon {
  width: 17px;
  height: 17px;

  vertical-align: bottom;
  margin-bottom: 2px;

  cursor: pointer;

  @include respond-to('medium') {
    width: 9px;
    height: 9px;
  }

  @include darkTheme {
    rect,
    circle {
      stroke: #064fa3;
    }

    path {
      stroke: $white;
    }

    rect {
      fill: #064fa3;
    }

    circle {
      fill: $white;
    }
  }
}

.titleMultiple {
  padding-top: 45px;
  color: #0f3971;
  font-weight: 600;
  font-size: 17px;

  @include respond-to('medium') {
    font-size: 12px;
  }

  @include darkTheme {
    color: $moderate-blue;
  }
}

.containerMultiple {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.labelContainer {
  justify-content: flex-start;
  line-height: 24px;
}

.typologyInfoItem {
  font-size: 14px;
  line-height: 24px;
}

.title {
  color: $base-middle-gray;
  margin-right: 16px;
}

.criteria {
  list-style: none;
  margin-block: 4px;
  margin-left: 32px;
  border: 1px solid $base-gray;
  border-radius: 4px;
  overflow: auto;

  & > li {
    background-color: white;
    padding: 16px;

    &:not(:last-child) {
      border-bottom: 1px solid $base-gray;
    }
  }
}
