@import 'utils/variables', 'utils/mixins';

.data_container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  font-size: 14px;
}

.data_input {
  display: flex;
  flex-direction: row;
  justify-content: space-between !important;
  align-items: flex-end;
  padding-top: 30px;

  @include respond-to('medium') {
    padding-top: 25px;
  }
}

.data_table {
  width: 100%;
  font-size: 18px;
  padding-top: 10px;
  border-bottom: 1px solid #d2e8ff;

  @include respond-to('medium') {
    font-size: 12px;
  }
}

.data_table_header {
  text-align: left;
  font-weight: normal;
  display: contents;
  color: #064fa3;
  vertical-align: middle;

  @include darkTheme {
    color: $moderate-blue;
  }
}

.data_table_header td {
  height: 60px;
  border-bottom: 1px solid #d2e8ff;
  font-weight: bold;

  @include respond-to('medium') {
    height: 45px;
  }
}

.data_table_tbody {
  color: #2a2f42;
  font-size: 16px;

  @include respond-to('medium') {
    font-size: 10px;
  }

  @include darkTheme {
    color: $white;
  }
}

.data_table_tbody td {
  height: 45px;

  @include respond-to('medium') {
    height: 40px;
  }
}

.data_big {
  width: 100%;
  height: 67px;

  @include respond-to('medium') {
    height: 35px;
  }
}

.data_big span {
  font-size: 17px;

  @include respond-to('medium') {
    font-size: 12px;
  }
}

.data_label {
  font-size: 14px;
  line-height: 18px;
  color: #064fa3;

  @include respond-to('medium') {
    font-size: 11px;
    line-height: 13px;
  }
}

.titleDetails {
  line-height: 24px;
}

.header {
  display: inline-block;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
}

.contractInfoItem {
  line-height: 24px;
}

.title {
  font-weight: 400;
  line-height: 24px;
  padding-right: 16px;
  color: $base-middle-gray;
}

.contractInfo {
  font-size: 14px;
  display: grid;
  gap: 4px;
}

.labelContainer {
  justify-content: flex-start;
}
