@import 'utils/variables', 'utils/mixins';

.data_container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.data_table {
  width: 100%;
  font-size: 18px;
  padding-top: 10px;
  border-bottom: 1px solid #d2e8ff;

  @include respond-to('medium') {
    font-size: 12px;
  }

  @include darkTheme {
    border-color: $grey;
  }
}

.data_table_header {
  text-align: left;
  font-weight: normal;
  display: contents;
  color: #064fa3;
  vertical-align: middle;

  @include darkTheme {
    color: $moderate-blue;
  }
}

.data_table_header td {
  height: 70px;
  border-bottom: 1px solid #d2e8ff;

  @include respond-to('medium') {
    height: 45px;
  }

  @include darkTheme {
    border-color: $grey;
  }
}

.data_table_tbody {
  color: #2a2f42;
  font-size: 16px;

  @include respond-to('large') {
    font-size: 15px;
  }

  @include respond-to('medium') {
    font-size: 10px;
  }

  @include darkTheme {
    color: $white;
  }
}

.data_table_tbody_title_parametrs {
  height: 45px;
  width: 28%;

  @include respond-to('medium') {
    height: 35px;
  }
}

.data_table_tbody_inn {
  line-height: 1.5;
  width: 22%;
}

.data_table_tbody_arrow {
  padding-left: 20px;
  padding-right: 10px;
}

.data_table_tbody_parametrs {
  width: 17%;
  font-weight: bold;
  text-align: right;

  svg {
    width: 15px !important;
    height: 17px !important;
    position: relative;
    top: 3px;
    margin-left: 3px;

    @include respond-to('medium') {
      width: 11px !important;
      height: 11px !important;
    }
  }
}

.data_action_button {
  margin-top: 50px;
  margin-bottom: 10px;
  width: 250px;
  height: 45px;

  line-height: 24px;

  @include respond-to('medium') {
    margin-top: 30px;
    width: 180px;
    height: 30px;
  }
}

.data_action_button span {
  font-size: 16px;
  line-height: 14px;

  @include respond-to('medium') {
    font-size: 11px;
  }
}

.radio input[type='radio'] {
  display: none;
}

.radio label {
  position: relative;

  font-size: 17px;
  color: #2a2f42;
  font-weight: 100;

  cursor: pointer;

  @include respond-to('medium') {
    font-size: 10px;
  }
}

.radio label:before {
  content: ' ';
  display: inline-block;
  position: relative;
  top: 2px;
  width: 16px;
  height: 16px;
  border-radius: 11px;
  border: 1px solid #4f99ee;
  background-color: transparent;

  @include respond-to('medium') {
    width: 10px;
    height: 10px;
  }
}

.radio label input[type='radio']:checked + span {
  border-radius: 11px;
  width: 13px;
  height: 13px;
  position: absolute;
  top: 2.7px;
  left: 2.7px;
  display: block;
  background-color: #4f99ee;

  @include respond-to('medium') {
    width: 8.5px;
    height: 8.5px;
    top: 0.5px;
    left: 1.9px;
  }
}

.data_big {
  width: 100%;
  height: 67px;
  margin-top: 20px;

  @include respond-to('medium') {
    height: 35px;
  }
}

.data_big span {
  font-size: 17px;

  @include respond-to('medium') {
    font-size: 12px;
  }
}

.data_table_tbody_gap {
  width: 15px;
  height: 15px;
}

.mainHeader {
  padding-left: 20px;
  padding-block: 8px;

  .period {
    color: #006ce8;
    font-size: 16px;
    line-height: 24px;
    font-weight: 700;
    margin-block: 4px;
    min-height: 24px;

    @include darkTheme {
      color: $moderate-blue;
    }

    .icon_calendar {
      margin-left: 10px;
    }
  }

  & > button {
    margin-top: -32px;
    margin-left: auto;
  }
}

.arrow {
  cursor: pointer;
}

.abdDetailed {
  display: flex;
  flex-direction: column;
  align-items: center;

  border-bottom: 1px solid #d2e8ff;
  padding-bottom: 15px;
}

.abdDetailedElement {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 95%;
  margin-top: 35px;

  @include respond-to('medium') {
    margin-top: 25px;
  }

  @include darkTheme {
    color: $white;
  }
}

.abdDetailedRow {
  display: flex;
  gap: 16px;

  &__header {
    margin-top: 10px;
  }

  @include darkTheme {
    color: $white;
  }
}

.abdDetailedTitle {
  font-size: 17px;
  font-weight: bold;

  @include respond-to('medium') {
    font-size: 12px;
  }
}

.abdDetailedTableHeader {
  color: $base-black;
  font-size: 16px;
  font-weight: 700;

  @include darkTheme {
    color: #859de0;
  }
}

.percentColumn {
  font-style: normal;
  font-weight: 600;
  cursor: pointer;
  text-align: right;

  color: #064fa3;

  @include darkTheme {
    color: #859de0;
  }
}

.tablePeriod {
  width: 100%;
}

.tablePeriodHeader {
  font-weight: normal;
  display: contents;
  color: $base-black;
  vertical-align: middle;
  font-size: 16px;

  td {
    height: 45px;
    border-bottom: 1px solid $base-dark-gray;
    font-weight: 600;
    padding-right: 10px;

    &:nth-child(1) {
      width: 15%;
    }

    &:nth-child(2) {
      width: 28%;
    }

    &:nth-child(3) {
      width: 28%;
    }

    &:nth-child(4) {
      width: 28%;
    }

    @include respond-to('medium') {
      height: 55px;
    }
  }

  @include darkTheme {
    color: $moderate-blue;
  }
}

.tablePeriodTbody {
  color: #2a2f42;
  font-size: 16px;

  tr {
    height: 45px;

    @include respond-to('medium') {
      height: 35px;
    }
  }

  tr td {
    border-bottom: 1px solid $base-dark-gray;
    padding-right: 10px;

    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  & > tr:last-child td {
    border-bottom: 0;
  }

  @include darkTheme {
    color: $white;
  }
}

.multiple_icon {
  width: 17px;
  height: 17px;

  vertical-align: bottom;
  margin-bottom: 2px;

  cursor: pointer;

  @include respond-to('medium') {
    width: 9px;
    height: 9px;
  }

  @include darkTheme {
    rect,
    circle {
      stroke: #064fa3;
    }

    path {
      stroke: $white;
    }

    rect {
      fill: #064fa3;
    }

    circle {
      fill: $white;
    }
  }
}

.titleMultiple {
  padding-top: 25px;
  color: #0f3971;
  font-weight: 600;
  padding-left: 30px;
  font-size: 17px;

  @include respond-to('medium') {
    font-size: 12px;
  }

  @include darkTheme {
    color: $moderate-blue;
  }
}

.containerMultiple {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.labelContainer {
  justify-content: flex-start;
}

.containerNameINN {
  display: flex;
  flex-direction: column;

  max-width: 95%;
  margin-top: 35px;
}

.headerNameINN {
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: left;

  color: $base-black;

  &:not(:first-child) {
    margin-top: 25px;
  }
}

.detailsNameINN {
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: left;

  margin-top: 3px;

  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;

  display: flex;
  flex-direction: row;
  justify-content: flex-start;

  span {
    color: $base-middle-gray;

    &:first-child {
      width: 220px;
    }
  }
}

.inn,
.name {
  color: $base-black !important;

  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.separateLine {
  border-bottom: 1px solid $base-dark-gray;
  margin: 15px -15px 5px -3px;
  width: 100%;
}

.header {
  display: inline-block;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
  margin-right: 3px;
}

.title {
  font-weight: 400;
  line-height: 24px;
  color: $base-middle-gray;
}

.askInfoItem {
  display: flex;
  align-items: center;
  gap: 16px;
  font-size: 14px;

  &.subitem {
    padding-left: 24px;
  }
}

.wrapper_creditDebit {
  display: flex;
}

.askInfo {
  flex: 0 1 50%;
  display: grid;
  gap: 4px;
}
