.ask-nds2__data__container {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
  }
  
  .ask-nds2__data__table {
    width: 100%;
    font-size: 18px;
    padding-top: 10px;
    border-bottom: 1px solid #D2E8FF;
  }
  
  .ask-nds2__data__table__header {
    text-align: left;
    font-weight: normal;
    display: contents;
    color: #064FA3;
    vertical-align: middle;
  }
  
  .ask-nds2__data__table__header td {
    height: 70px;
    border-bottom: 1px solid #D2E8FF;
  }
  
  .ask-nds2__data__table__tbody {
    color: #2A2F42;
    font-size: 16px;
  }
  
  .ask-nds2__data__table__tbody_title_parametrs {
    height: 45px;
    width: 30%;
  }
  
  .ask-nds2__data__table__tbody_inn {
    line-height: 1.5;
    width: 27%;
  }
  
  .ask-nds2__data__table__tbody_parametrs {
    width: 16%;
    font-weight: bold;
    text-align: right;
  }
  
  .ask-nds2__data__action__button {
    margin-top: 50px;
    margin-bottom: 10px;
    width: 250px;
    height: 45px;
  
    line-height: 24px;
  }
  
  .ask-nds2__data__action__button span {
    font-size: 16px;
    line-height: 14px;
  }
  
  .ask-nds2__radio input[type='radio'] {
    display: none;
  }
  
  .ask-nds2__radio label {
    position: relative;
  
    font-size: 17px;
    color: #2A2F42;
    font-weight: 100;
  
    cursor: pointer;
  }
  
  .ask-nds2__radio label:before{
    content: " ";
    display: inline-block;
    position: relative;
    top: 2px;
    width: 16px;
    height: 16px;
    border-radius: 11px;
    border: 1px solid #4F99EE;
    background-color: transparent;
  }
  
  .ask-nds2__radio label input[type='radio']:checked+span {
    border-radius: 11px;
    width: 13px;
    height: 13px;
    position: absolute;
    top: 2.7px;
    left: 2.7px;
    display: block;
    background-color: #4F99EE;
  }
  
  @media only screen and (max-width: 1170px) {
    .ask-nds2__data__table {
      width: 100%;
      font-size: 12px;
      cellspacing: 0;
      padding-top: 10px;
    }
  
    .ask-nds2__data__table__header {
      text-align: left;
      font-weight: normal;
      display: contents;
      color: #064FA3;
      vertical-align: middle;
    }
  
    .ask-nds2__data__table__tbody {
      color: #2A2F42;
      font-size: 10px;
    }
  
    .ask-nds2__data__table__header td {
      height: 45px;
      border-bottom: 1px solid #D2E8FF;
    }
  
    .ask-nds2__data__table__tbody_title_parametrs {
      height: 35px;
      width: 30%;
    }
  
    .ask-nds2__data__table__tbody_inn {
      line-height: 1.5;
      width: 27%;
    }
  
    .ask-nds2__data__table__tbody_parametrs {
      width: 16%;
      font-weight: bold;
      text-align: right;
    }
  
    .ask-nds2__data__action__button {
      margin-top: 30px;
      margin-bottom: 10px;
      width: 180px;
      height: 30px;
  
      line-height: 24px;
    }
  
    .ask-nds2__data__action__button span {
      font-size: 11px;
    }
  
    .ask-nds2__radio label:before {
      top: 2px;
      width: 10px;
      height: 10px;
    }
  
    .ask-nds2__radio label {
      font-size: 10px;
    }
  
    .ask-nds2__radio label input[type='radio']:checked+span {
      width: 8.5px;
      height: 8.5px;
      top: 0.5px;
      left: 1.9px;
    }
  }

  .ask-nds2__data__table__tbody_gap {
      position: relative;
      top: 4px;
  }
