@import 'utils/variables', 'utils/mixins';

.header {
  .period {
    color: #006ce8;
    font-size: 16px;
    line-height: 24px;
    font-weight: 700;
    margin-block: 4px;
    min-height: 24px;

    @include darkTheme {
      color: $moderate-blue;
    }

    .icon_calendar {
      margin-left: 9px;
    }
  }

  & > button {
    margin-top: -32px;
    margin-left: auto;
  }
}
