@import '../../../styles/utils/variables', '../../../styles/utils/mixins';

.header {
  display: flex;
  align-items: center;
  gap: 12px;
}

.container {
  width: 1082px;
  max-width: 100%;
  min-height: calc(100vh - 210px);
}

.action_button_data_container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  padding-left: 56px;
  padding-right: 56px;
  padding-bottom: 30px;

  @include respond-to('medium') {
    padding-left: 30px;
    padding-right: 30px;
  }
}

.data_action_button {
  margin-top: 50px;
  margin-bottom: 10px;
  width: 300px;
  height: 45px;

  line-height: 24px;

  @include respond-to('medium') {
    margin-top: 30px;
    width: 200px;
    height: 30px;
  }
}

.data_action_button span {
  font-size: 16px;
  line-height: 14px;

  @include respond-to('medium') {
    font-size: 11px;
  }
}

.loader {
  margin-top: 50px;
  width: 100%;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
}

.data_container {
  flex: 1 1 auto;
}

.input_container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.data_big {
  width: 100%;
  height: 67px;
  margin-top: 20px;

  &.required span::after {
    content: ' *';
    color: #ff6161;
  }

  &.error input {
    border: 1px solid #ff6161;
  }

  @include respond-to('medium') {
    height: 35px;
  }
}

.data_big span {
  font-size: 17px;

  @include respond-to('medium') {
    font-size: 12px;
  }
}

.mainDropdownContainer {
  margin-top: 20px;
}

.data_big input:disabled {
  background-color: #efefef;
  color: $light-grey;

  @include darkTheme {
    background-color: $black-grey-light;
    color: $grey;
  }
}

.dropdownBig {
  width: 100%;
  height: 67px;

  font-weight: 300;

  flex-direction: column;
  align-items: flex-start;

  margin-top: 2px;

  @include respond-to('medium') {
    height: 44px;
  }
}

.dropdownContainer {
  width: 100%;
  height: 45px;

  padding: 12px 20px !important;
  align-items: center;

  display: flex;
  justify-content: space-between;

  border: 1px solid $light-blue;
  box-sizing: border-box;
  border-radius: 8px;

  background-color: #efefef;

  @include respond-to('medium') {
    height: 30px;
  }

  &.error {
    border: 1px solid #ff6161 !important;
  }

  @include darkTheme {
    background: $black-blue;
  }
}

.dropdownLabel {
  font-size: 17px;
  line-height: 18px;
  font-weight: 400;
  color: $dark-blue;

  @include respond-to('medium') {
    font-size: 12px;
    line-height: 12px;
  }

  &.required::after {
    content: ' *';
    color: #ff6161;
  }

  @include darkTheme {
    color: $moderate-blue;
  }
}

.dropdownValue {
  font-size: 16px;
  line-height: 20px;
  color: #adadad;

  @include respond-to('medium') {
    font-size: 12px;
    line-height: 14px;
  }
}

.fileContainer {
  padding-block: 8px;
}

.fileList {
  display: flex;
  flex-flow: row wrap;
}

.fileItem {
  cursor: pointer;
  margin-top: 8px;
  margin-right: 20px;
  padding: 8px 12px;
  display: flex;
  flex-direction: row;
  align-items: center;
  border-radius: 4px;
  box-sizing: border-box;
  width: 100%;
  border: 1px solid $base-dark2-gray;
  gap: 12px;
  min-height: 40px;

  .fileItemLabel {
    font-size: 14px;
  }
}

.fileItemRemove {
  margin-left: 22px;
  width: 9px;
  height: 9px;

  cursor: pointer;

  @include respond-to('medium') {
    width: 8px;
    height: 8px;
  }
}

.data_label {
  font-weight: 700;
  margin-bottom: 4px;
}

.headerFixed {
  top: 0;
  background-color: white;

  @include darkTheme {
    background: $black-blue;
  }
}

.separateLine {
  border-bottom: 1px solid $base-gray;
  width: 111%;
}

.section {
  padding-top: 8px !important;
  padding-bottom: 0 !important;

  & > div:first-child {
    padding-left: 24px;
  }
}

.title {
  display: flex;
  flex-direction: column;
}

.subtypeName {
  font-size: 16px;
  line-height: 24px;
  color: $base-black;
  font-weight: 700;
  padding-block: 8px;

  display: flex;
  align-items: center;
  gap: 12px;

  @include darkTheme {
    color: $moderate-blue;
  }
}

.containerMainInfo {
  display: flex;
  flex-direction: row;
}

.lineIcon {
  display: flex;
  align-items: center;

  & > svg {
    margin-top: 4px;
  }
}

.containerName {
  display: flex;
  flex-direction: column;
  max-width: 95%;
  font-weight: 100;
}

.headerName {
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  padding: 8px;
  display: flex;
  gap: 16px;
}

.containerDiv {
  background-color: $light-gray;
  padding-block: 16px 8px;
  padding-inline: 96px 24px;
  border-top: 1px solid #e6e6e6;
  border-bottom: 1px solid #e6e6e6;
  display: grid;
  gap: 8px;
}

.footer {
  display: flex;
  justify-content: flex-end;
}

.accordionOuter {
  :global(.arrow_front) {
    margin-right: 13px;
  }
}
