@import 'utils/mixins', 'utils/variables';

.data_container {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: flex-end;
}

.data_table {
  width: 100%;
  border-collapse: collapse;
  font-size: 18px;
  padding-top: 10px;
  table-layout: fixed;

  @include respond-to('medium') {
    font-size: 12px;
  }
}

.data_table_header {
  text-align: left;
  font-weight: 700;
  font-size: 16px;
  display: contents;
  color: $base-black;
  vertical-align: middle;

  @include darkTheme {
    color: $moderate-blue;
  }
}

.data_table_header th {
  height: 32px;
  border-bottom: 1px solid $base-dark2-gray;

  &:first-child {
    width: 160px;
  }

  &:nth-child(2) {
    width: 260px;
  }

  @include respond-to(medium) {
    &:first-child {
      width: 145px;
    }

    &:nth-child(2) {
      width: 170px;
    }
  }
}

.data_table_tbody {
  color: #2a2f42;
  font-size: 14px;

  td {
    height: 32px;
    border-bottom: 1px solid $base-dark-gray;
    padding-block: 8px;

    &:not(:last-child) {
      padding-right: 16px;
    }
  }

  & > tr:last-child td {
    border-bottom: 0;
  }

  @include darkTheme {
    color: $white;
  }
}

.icon_cell {
  width: 30px;
}

.labelContainer {
  justify-content: flex-start;
}

.icon_person {
  margin-right: 8px;
}

.user {
  display: flex;
}
