@import 'utils/variables', 'utils/mixins';

.container {
  display: grid;
  gap: 4px;

  & > h4 {
    font-size: 16px;
    line-height: 20px;
    margin-block: 2px;
  }

  .value {
    font-size: 14px;
    line-height: 24px;
  }

  .params {
    border-radius: 4px;
    border: 1px solid $base-gray;
    padding: 8px 16px;

    & > span {
      display: inline-block;
      font-size: 14px;
      line-height: 24px;
      margin-bottom: 10px;
      color: $base-middle-gray;
    }

    & > dl {
      display: grid;
      grid: auto / 2fr 1fr;
      gap: 8px 32px;

      dt,
      dd {
        font-size: 14px;
        line-height: 18px;
      }
    }
  }
}
